//
import React from 'react';
import './../../css/individual.css';
export default class Individual extends React.Component{
    render(){
        return(
            <div id="individual">
                <h2></h2>
                <div className="personalform">
                    <p>
                        <label htmlFor="phone"></label>
                        <input type="text" id="phone" placeholder=""/>
                    </p>
                    <p>
                        <label htmlFor="nickname"></label>
                        <input type="text" id="nickname" placeholder=""/>
                    </p>
                    <p>
                        <label></label>
                        <input type="radio" id="man" name="sex" value="man" checked/>
                        &nbsp;&nbsp;&nbsp;&nbsp;
                        <input type="radio" id="woman" name="sex" value="woman"/>
                    </p>
                    <p>
                        <label htmlFor="signature"></label>
                        <input type="text" id="signature" placeholder=""/>
                    </p>
                    <p>
                        <input type="button" id="individualbtn" value=""/>
                    </p>
                </div>
            </div>
        );
    }
}


