//
import React from "react";
import './../css/forget.css';
import {Link} from 'react-router-dom';
import logo from './../images/logo.png';
import Cookies from 'js-cookie';
import md5 from "js-md5";
export default class Forget extends React.Component{
    constructor(){
        super();
        this.state={
            data:0,
            errormsg:"",
            tell:"",
            repass:"",
            codes:""
        }
    }
    componentWillMount(){
        if(Cookies.get('name')){
            this.props.history.push('/login');
        }
    }
    componentDidMount(){this.setState({data:1});}
    componentDidUpdate(){
        const that=this;
        let {tell,repass,codes}=this.state;
        const count=document.getElementById('count');
        const code=document.getElementById('code');
        const codebtn=document.getElementById('codebtn');
        const pwd=document.getElementById('pwd');
        const repwd=document.getElementById('repwd');
        const forgetbtn=document.getElementById('forgetbtn');
        count.onblur=function(){
            const reg=/^1[34578]\d{9}/;
            if(this.value===""){
                that.setState({errormsg:""});
            }else{
                if(!reg.test(this.value)){
                    that.setState({errormsg:""});
                }else{
                    that.setState({tell:this.value,errormsg:""});
                }
            }
        };
        codebtn.onclick=function(){
            if(tell===""){
                that.setState({errormsg:""});
            }else{
                fetch("http://127.0.0.1:3001/api/auth/send_code?tel="+tell+"&type=2"+"&s="+md5(tell+"RJ5HXXCy5SH869n%4^aAu2p@Mk22sEF4"))
                    .then(res=>res.json())
                    .then(res=>{
                        if(parseInt(res.success)===0){
                            that.setState({errormsg:res.message});
                        }else{
                            console.log(res);
                            alert('');
                        }});
            }
        };
        //
        code.onblur=function(){
            if(this.value===""){
                that.setState({errormsg:""});
            }else{
                that.setState({codes:this.value});
            }
        };
        //
        pwd.onblur=function(){
            //6-18
            const reg=/^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,18}$/;
            if(this.value===""){
                that.setState({errormsg:""});
            }else{
                if(!reg.test(this.value)){
                    that.setState({errormsg:""});
                }else{
                    that.setState({errormsg:""});
                }
            }
        };
        //
        repwd.onblur=function(){
            if(pwd.value!==this.value){
                that.setState({errormsg:""});
            }else{
                that.setState({errormsg:"",repass:this.value});
            }
        };
        //
        forgetbtn.onclick=function(){
            if(repass===""){return false}
            fetch("http://admin9.lohas-travel.com/api/auth/forget_password?tel="+tell+"&code="+codes+"&password="+repass)
                .then(res=>res.json()).then(res=>{
                if(parseInt(res.success)===0){
                    that.setState({errormsg:res.message});
                }else{
                    that.setState({errormsg:""});
                    alert('');
                    that.props.history.push('/login');
                }
            });
        };
    }

    render(){
        let {errormsg}=this.state;
        return(
            <div id="forget">
                {/**/}
                <div className="header">
                    <div className="w">
                        <Link to="/" className="logo">
                            <img src={logo} alt=""/>
                            <h1>- </h1>
                        </Link>
                    </div>
                </div>
                {/**/}
                {/**/}
                <div className="content">
                    <div className="w">
                        <div className="formwrapper">
                            <form>
                                <h1></h1>
                                <div style={{height:20,color:"red"}}>{errormsg}</div>
                                <div className="backtologin"><Link to='/login'></Link></div>
                                <h2> </h2>
                                <p>
                                    <input type="text" id="count" name="count" placeholder=""/>
                                </p>
                                <p className="code">
                                    <input type="text" id="code" name="code" placeholder="(30)"/>
                                    <input type="button" id="codebtn" value=""/>
                                </p>
                                <h2> </h2>
                                <p>
                                    <input type="password" id="pwd" name="pwd" placeholder="6-18"/>
                                </p>
                                <p>
                                    <input type="password" id="repwd" name="repwd" placeholder=""/>
                                </p>
                                <p>
                                    <input type="button" value="" id="forgetbtn"/>
                                </p>
                            </form>
                        </div>
                    </div>
                </div>
                {/**/}
                {/**/}
                <div className="footer">Copyright  2005-2018   ICP15003740</div>
                {/**/}
            </div>
        );
    }
}


