import React from 'react';
import recruit from './../../images/recruit.jpg';
export default class Recruit extends React.Component{
    render(){
        return(
            <div id="recruit">
                <img src={recruit} alt=""/>
                <h2></h2>
                <p></p>
                <p></p>
                <p></p>
                <h2>:</h2>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p></p>
                <p>,</p>
                <p><strong></strong></p>
                <p>: <span style={{color:"deepskyblue"}}>hr@lohas-travel.com</span></p>
                <p></p>
            </div>
        );
    }
}


