//
import React, {Component} from 'react';
import {Link} from 'react-router-dom';
import Cookies from 'js-cookie';
import './../css/home.css';
import Daypicker from './component/daypicker';
import Datepicker1 from './component/datepicker1';
import Searchbtn from './component/searchbtn';
import Textinput from "./component/textinput";
import Textinput2 from "./component/textinput2";
import Hotcity from "./component/hotcity";
import Partner from "./component/partner";
import Footer from "./component/footer";

import logo from './../images/logo.png';
import hotel01 from './../images/hotel_icon01.png';
import hotel from './../images/hotel_icon.png';
import air from './../images/air_icon.png';
import air01 from './../images/air_icon01.png';
import scenic from './../images/scenic_icon.png';
import scenic01 from './../images/scenic_icon01.png';
import activity from './../images/activity_icon.png';
import activity01 from './../images/activity_icon01.png';
import appcode from './../images/appcode.png';
import wxcode from './../images/wxcode.png';

export default class Home extends Component {
    constructor(){
        super();
        this.state={
            name:Cookies.get('name')
        }
    }
    componentDidMount(){
        const homefocus=document.getElementById('homefocus');
        //IE
        const Cheight=document.documentElement.clientHeight||document.body.offsetHeight;
        homefocus.style.height=Cheight+"px";

        const that=this;

        //id
        function getID(val){
            return document.getElementById(val);
        }

        //
        //js
        Array.prototype.indexOf=function(val){
            for(let i=0; i<this.length; i++){
                if(this[i]==val) return i;
            }
            return -1;
        };
        //js
        Array.prototype.remove = function(val) {
            let index = this.indexOf(val);
            if (index > -1) {
                this.splice(index, 1);
            }
        };

        let starttime="";//  
        let endtime="";// 

        //
        function timepicker(input,calendar,start,text){
            input.onfocus=function () {
                calendar.parentElement.style.display="block";
                calendar.style.display="block";
                let otd=calendar.getElementsByTagName('td');
                for(let i=0; i<otd.length; i++){
                    otd[i].onclick=function(){
                        //
                        let cur=Cookies.get('curday');
                        let time=this.getAttribute("data");
                        if(time<cur){
                            alert(text);
                            time=cur;
                        }else{
                            time=this.getAttribute("data");
                        }
                        let year=time.substring(0,4);
                        let month=time.substring(4,6);
                        let day=time.substring(6,8);
                        input.value=year+"-"+month+"-"+day;
                        start=time;
                        calendar.style.display="none";
                        calendar.parentElement.style.display="none";
                    }
                }
            }
        }

        //
        function timespicker(input,calendar,endinput,text){
            input.onfocus=function(){
                calendar.parentElement.style.display="block";
                calendar.style.display="block";
                let otd=calendar.getElementsByTagName('td');
                for(let i=0; i<otd.length; i++){
                    otd[i].onclick=function(){
                        //
                        let cur=Cookies.get('curday');
                        let time=this.getAttribute("data");
                        if(time<cur){
                            alert("");
                            time=cur;
                        }else{
                            time=this.getAttribute("data");
                        }
                        let year=time.substring(0,4);
                        let month=time.substring(4,6);
                        let day=time.substring(6,8);
                        input.value=year+"-"+month+"-"+day;
                        starttime=time;
                        if(endtime!==""&&starttime>endtime){
                            alert(text);
                            endinput.value="";
                        }
                        calendar.style.display="none";
                        calendar.parentElement.style.display="none";
                    }
                }
            }
        }

        //
        function timespicker2(input,calendar,endinput,text){
            input.onfocus=function(){
                calendar.parentElement.style.display="block";
                calendar.style.display="block";
                let otd=calendar.getElementsByTagName('td');
                for(let i=0; i<otd.length; i++){
                    otd[i].onclick=function(){

                        //
                        let cur=Cookies.get('curday');
                        let time=this.getAttribute("data");
                        if(time<cur){
                            alert("");
                            time=cur;
                        }else{
                            time=this.getAttribute("data");
                        }
                        let year=time.substring(0,4);
                        let month=time.substring(4,6);
                        let day=time.substring(6,8);
                        input.value=year+"-"+month+"-"+day;
                        endtime=time;
                        if(endtime!==""&&starttime>endtime){
                            alert(text);
                            endinput.value="";
                        }
                        calendar.style.display="none";
                        calendar.parentElement.style.display="none";
                    }
                }
            }
        }

        //
        function shopchoose(Ele,arr){
            Ele.onmouseover=function() {
                let checkSpan = this.getElementsByTagName('span');
                for (let i = 0; i < checkSpan.length; i++) {
                    checkSpan[i].onclick = function () {
                        if(checkSpan[i].className===""){
                            this.className = "active";
                            arr.push(this.getAttribute('data-type'));
                        }else{
                            this.className = "";
                            arr.remove(this.getAttribute('data-type'));
                        }
                    }
                }
            }
        }

        //
        let searchlists=getID('searchlist').getElementsByClassName('item');//
        let searchlistsimg=getID('searchlist').getElementsByTagName('img');//
        let searchboxwrapper=getID('searchwrapper').getElementsByClassName('boxwrapper')[0];//
        searchlists[0].style.color="#ff9f00";
        searchlistsimg[0].src=hotel01;
        for(let i=0; i<searchlists.length;i++){
            searchlists[i].index=i;
            searchlists[i].onclick=function(){
                starttime="";//
                endtime="";//
                if(i===0){
                    searchlistsimg[0].src=hotel01;
                    searchlistsimg[1].src=air;
                    searchlistsimg[2].src=scenic;
                    searchlistsimg[3].src=activity;
                }else if(i===1){
                    searchlistsimg[0].src=hotel;
                    searchlistsimg[1].src=air01;
                    searchlistsimg[2].src=scenic;
                    searchlistsimg[3].src=activity;
                }else if(i===2){
                    searchlistsimg[0].src=hotel;
                    searchlistsimg[1].src=air;
                    searchlistsimg[2].src=scenic01;
                    searchlistsimg[3].src=activity;
                }else{
                    searchlistsimg[0].src=hotel;
                    searchlistsimg[1].src=air;
                    searchlistsimg[2].src=scenic;
                    searchlistsimg[3].src=activity01;
                }
                for(let i=0; i<searchlists.length;i++){
                    searchlists[i].style.color="#fff";
                }
                this.style.color="#ff9f00";
                searchboxwrapper.style.left=-this.index*1200+"px";
            }
        }

        //
        let hotelsearch=getID('hotelsearch');
        let livetime=getID('livetime');
        let leavetime=getID('leavetime');
        let livecalendar=getID('livecalendar');
        let leavecalendar=getID('leavecalendar');
        let hotelcheck=getID('hotelcheck');
        let hotelshop=["1"];
        //
        //
        timespicker(livetime,livecalendar,leavetime,"");
        //
        timespicker2(leavetime,leavecalendar,leavetime,"");
        //
        shopchoose(hotelcheck,hotelshop);
        //
        let hotelbtn=getID('hotelbtn');
        hotelbtn.onclick=function(){
            if(hotelsearch.value===""){
                alert("");
            }else{
                that.props.history.push({pathname:'/hotel/'+hotelsearch.value});
            }
        };

        //
        let airfrom=getID('airfrom');
        let airto=getID('airto');
        let airfromtime=getID('airfromtime');
        let airtotime=getID('airtotime');
        let airfromcalendar=getID('airfromcalendar');
        let airtocalendar=getID('airtocalendar');
        let airbtn=getID('airbtn');
        let aircheck=getID('aircheck');
        let airshop=["1"];
        //
        timespicker(airfromtime,airfromcalendar,airtotime,"");
        //
        timespicker2(airtotime,airtocalendar,airtotime,"");
        //
        shopchoose(aircheck,airshop);
        //
        airbtn.onclick=function(){
            if(airfrom.value===""){
                alert('');
            }else if(airto.value===""){
                alert('');
            }else if(airfromtime.value===""){
                alert('');
            }else{
                if(airshop.length>0){
                    for(let i=0; i<airshop.length; i++){
                        fetch('http://admin9.lohas-travel.com/api/flight/flight_url?departCity='+airfrom.value+'&arriveCity='
                            +airto.value+'&departDate='+airfromtime.value+'&arriveDate='+airtotime+'&type='+airshop[i])
                            .then(res=>res.json())
                            .then(res=>{
                                if(res.success===0){
                                    alert('');
                                }else{
                                    window.open(res.data.url);
                                }
                            });
                    }
                }else{
                    fetch('http://admin9.lohas-travel.com/api/flight/flight_url?departCity='+airfrom.value+'&arriveCity='
                        +airto.value+'&departDate='+airfromtime.value+'&arriveDate='+airtotime+'&type=1')
                        .then(res=>res.json())
                        .then(res=>{
                            if(res.success===0){
                                alert('');
                            }else{
                                window.open(res.data.url);
                            }
                        });
                }
            }
        };

        //
        let sitesearch=getID('sitesearch');
        let sitefrom=getID('sitefrom');
        let sitebtn=getID('sitebtn');
        let sitecheck=getID('sitecheck');
        let siteshop=["1"];
        let sitecalendar=getID('sitecalendar');
        let sitefromtime="";
        //
        timepicker(sitefrom,sitecalendar,sitefromtime);
        //
        shopchoose(sitecheck,siteshop);
        //
        sitebtn.onclick=function(){
            if(sitesearch.value===""){
                alert("");
            }else{
                if(siteshop.length>0){
                    for(let i=0; i<siteshop.length; i++){
                        switch(siteshop[i]){
                            case "3":
                                window.open('https://so.ly.com/scenery?refid=48880358&q=' + sitesearch.value);
                                break;
                            case "5":
                                window.open('http://s.lvmama.com/ticket/?keyword=' + sitesearch.value);
                                break;
                            default:
                                window.open('http://piao.ctrip.com/piao.html?allianceid=26524&sid=467134&keyword=' + sitesearch.value);
                        }
                    }
                }else{
                    window.open('http://piao.ctrip.com/piao.html?allianceid=26524&sid=467134&keyword='+ sitesearch.value);
                }
            }
        };

        //
        let activitysearch=getID('activitysearch');
        let activityfrom=getID('activityfrom');
        let activitybtn=getID('activitybtn');
        let activitycheck=getID('activitycheck');
        let activitycalendar=getID('activitycalendar');
        let activityshop=["1"];
        let activityfromtime="";
        //
        timepicker(activityfrom,activitycalendar,activityfromtime);
        //
        shopchoose(activitycheck,activityshop);
        //
        activitybtn.onclick=function(){
            if(activitysearch.value===""){
                alert('');
            }else{
                if(activityshop.length>0){
                    for(let i=0; i<activityshop.length; i++){
                        switch(activityshop[i]){
                            case "3":
                                window.open('https://so.ly.com/scenery?q=' + activitysearch.value);
                                break;
                            default:
                                window.open('http://vacations.ctrip.com/whole-213B126/?searchValue=' + activitysearch.value + '&searchText=' + activitysearch.value  + '&from=do')
                        }
                    }
                }else{
                    window.open('http://vacations.ctrip.com/whole-213B126/?searchValue=' + activitysearch.value + '&searchText=' + activitysearch.value  + '&from=do')
                }
            }
        };

        //APP
        let sidedownload=getID('sidedownload');
        let sidedownpop=getID('sidedownpop');
        let closebtn=getID('closebtn');
        sidedownload.onclick=function(){
            this.style.left="100%";
            sidedownpop.style.left=0;
            if(this.style.left==="100%"){
             let timmer=window.setTimeout(function(){
                    sidedownload.style.left=0;
                    sidedownpop.style.left="-100%";
                },10000);
            }else{
               return false;
            }
        };
        closebtn.onclick=function(){
            sidedownload.style.left=0;
            sidedownpop.style.left="-100%";
        };

        //
        let totop=getID('totop');
        let sHeight=0;
        totop.onclick=function(){
            sHeight=document.documentElement.scrollTop||document.body.scrollTop;
            let timmer=window.setInterval(function(){
                sHeight-=20;
                if(document.documentElement.scrollTop){
                    document.documentElement.scrollTop=sHeight;
                }else{
                    document.body.scrollTop=sHeight;
                }
            },8);
            window.onscroll=function(){
                sHeight=document.documentElement.scrollTop||document.body.scrollTop;
                if(sHeight===0){
                    window.clearInterval(timmer);
                    timmer=null;
                }
            }
        };
    }
    //Can only update a mounted or mounting component 
    componentWillUnmount(){
        //setState
        this.setState=(state,callback)=>{return};
    }
    goout(){
        Cookies.remove('name');
        this.setState({name:undefined});
    }
    render() {
        const name=this.state.name;
        return (
            <div className="home" id="home">
                {/**/}
                <div id="homeheader">
                    <div className="wrapper">
                        {
                            name!==undefined?
                            (
                            <span className="left">
                                <Link to="/personal"></Link>
                                <a href="javascript:;" onClick={this.goout.bind(this)}></a>
                            </span>
                            ):
                            (
                            <span className="left">
                                <Link to="/login"></Link>
                                <Link to="/regist"></Link>
                            </span>
                            )
                        }
                        <span className="right">
                            <Link to="/company/connect"></Link>
                            <Link to="/company/download">APP</Link>
                         </span>
                    </div>
                </div>
                {/**/}
                {/*focus*/}
                <div id="homefocus">
                    {/*logo*/}
                    <div className="logo w">
                        <img src={logo} alt=""/>
                        <h1 className="logotitle"></h1>
                        <p className="logosubtitle"></p>
                        <p className="slogan"></p>
                    </div>
                    {/**/}
                    <div id="searchlist" className="w">
                        <div className="item">
                            <img src={hotel} alt=""/>
                            <p></p>
                        </div>
                        <div className="item">
                            <img src={air} alt=""/>
                            <p></p>
                        </div>
                        <div className="item">
                            <img src={scenic} alt=""/>
                            <p></p>
                        </div>
                        <div className="item">
                            <img src={activity} alt=""/>
                            <p></p>
                        </div>
                    </div>
                    {/**/}
                    <div id="searchwrapper" className="w">
                        <div className="boxwrapper">
                            {/**/}
                            <div className="item hotelsearch">
                                <div className="input">
                                    <Textinput placeholder="" wth="380px" id="hotelsearch"/>
                                    <Datepicker1  id="livetime" placeholder="()"/>
                                    <Datepicker1  id="leavetime" placeholder="()"/>
                                </div>
                                <Searchbtn id="hotelbtn"/>
                                {/**/}
                                <div  className="shopcheck" id="hotelcheck">
                                    <span className="active" data-type="1"></span>
                                    <span data-type="5"></span>
                                    <span data-type="2">booking</span>
                                    <span data-type="3"></span>
                                    <span data-type="4"></span>
                                    <span data-type="6"></span>
                                </div>
                                {/**/}
                                <div className="datewrapper">
                                    <Daypicker id="livecalendar"/>
                                    <Daypicker id="leavecalendar"/>
                                </div>
                                {/**/}
                            </div>
                            {/**/}
                            <div className="item airsearch">
                                <div className="input">
                                    <Textinput2 placeholder="" id='airfrom'/>
                                    <Textinput2 placeholder="" mleft="70px" id="airto"/>
                                    <Datepicker1 placeholder="" id="airfromtime"/>
                                    <Datepicker1 placeholder="()" id="airtotime"/>
                                </div>
                                <Searchbtn id="airbtn"/>
                                {/**/}
                                <div  className="shopcheck" id="aircheck">
                                    <span className="active" data-type="1"></span>
                                    <span data-type="3"></span>
                                    <span data-type="4"></span>
                                </div>
                                {/**/}
                                <div className="datewrapper">
                                    <Daypicker id="airfromcalendar"/>
                                    <Daypicker id="airtocalendar"/>
                                </div>
                                {/**/}
                            </div>
                            {/**/}
                            <div className="item scenicsearch">
                                <div className="input">
                                    <Textinput placeholder="..." wth="675px" id="sitesearch"/>
                                    <Datepicker1 placeholder="" id="sitefrom"/>
                                </div>
                                <Searchbtn id="sitebtn"/>
                                {/**/}
                                <div  className="shopcheck" id="sitecheck">
                                    <span className="active" data-type="1"></span>
                                    <span data-type="5"></span>
                                    <span data-type="3"></span>
                                </div>
                                {/**/}
                                <div className="datewrapper">
                                    <Daypicker id="sitecalendar"/>
                                </div>
                                {/**/}
                            </div>
                            {/**/}
                            <div className="item activitysearch">
                                <div className="input">
                                    <Textinput placeholder="..." wth="675px" id="activitysearch"/>
                                    <Datepicker1 placeholder="" id="activityfrom"/>
                                </div>
                                <Searchbtn id="activitybtn"/>
                                {/**/}
                                <div  className="shopcheck" id="activitycheck">
                                    <span className="active" data-type="1"></span>
                                    <span data-type="3"></span>
                                </div>
                                {/**/}
                                <div className="datewrapper">
                                    <Daypicker id="activitycalendar"/>
                                </div>
                                {/**/}
                            </div>
                        </div>
                    </div>
                </div>
                {/*focus*/}
                {/**/}
                <Hotcity/>
                {/**/}
                {/**/}
                <Partner/>
                {/**/}
                {/**/}
                <Footer/>
                {/**/}
                {/**/}
                <div id="totop"><p> </p></div>
                {/**/}
                {/**/}
                <div id="sidedownload">
                    <img src={logo} alt="APP"/>
                    <p>APP</p>
                    <p></p>
                </div>
                <div id="sidedownpop">
                    <div className="w" style={{position:"relative"}}>
                        <img className="logo" src={logo} alt=""/>
                        <div className="left">
                            <h3></h3>
                            <h1></h1>
                        </div>
                        <div className="right">
                            <div className="code">
                                <img src={appcode} alt=""/>
                                <p>APP</p>
                            </div>
                            <div className="code">
                                <img src={wxcode} alt=""/>
                                <p></p>
                            </div>
                        </div>
                        <div id="closebtn"> </div>
                    </div>
                </div>
                {/**/}
            </div>
        );
    }
}




