import React from 'react';
import {Link} from 'react-router-dom';
import Cookies from 'js-cookie';
import './../css/login.css';
import logo from './../images/logo.png';
export default class Login extends React.Component{
    constructor(){
        super();
        this.state={
            data:"",
            errormsg:""
        }
    }
    componentWillMount(){
        if(Cookies.get('name')){
            this.props.history.push('/personal');
        }
    }
    componentDidMount(){
        this.setState({data:1});
    }
    componentDidUpdate(){
        const that=this;
        const count=document.getElementById('count');
        const pwd=document.getElementById('pwd');
        const loginbtn=document.getElementById('loginbtn');
        loginbtn.onclick=function(){
           let countval=count.value;
           let pwdval=pwd.value;
           fetch("http://admin9.lohas-travel.com//api/auth/user_signin?tel="+countval+"&password="+pwdval)
               .then(res=>res.json()).then(res=>{
                   if(res.success===0){
                       that.setState({errormsg:res.message});
                   }else{
                       Cookies.set('name',countval,{expires:1});
                       that.props.history.push('/personal');
                   }
           });
        }
    }
    render(){
        return(
            <div id="login">
                {/**/}
                <div className="header">
                    <div className="w">
                        <Link to="/" className="logo">
                            <img src={logo} alt=""/>
                            <h1>- </h1>
                        </Link>
                    </div>
                </div>
                {/**/}
                {/**/}
                <div className="content">
                    <div className="w">
                        <div className="formwrapper">
                            <form>
                                <h1></h1>
                                <div style={{color:"red",height:25}}>{this.state.errormsg}</div>
                                <p>
                                    <label htmlFor="count" className="countlabel"> </label>
                                    <input type="text" id="count" name="count" placeholder="" maxLength="11"/>
                                </p>
                                <p>
                                    <label htmlFor="pwd" className="pwdlabel"> </label>
                                    <input type="password" id="pwd" name="pwd" placeholder="" maxLength="18"/>
                                </p>
                                <p>
                                    <input type="button" defaultValue="" id="loginbtn"/>
                                </p>
                                <p>
                                    <Link to="/forget" style={{float:"right"}}></Link>
                                    <Link to="/regist"></Link>,!
                                </p>
                            </form>
                        </div>
                    </div>
                </div>
                {/**/}
                {/**/}
                <div className="footer">Copyright  2005-2018   ICP15003740</div>
                {/**/}
            </div>
        );
    }
}


