//
import React from 'react';
import {Link} from 'react-router-dom';
import moment from 'moment';
import './../css/hotel.css';
import Header from './component/header';
import Footer from "./component/footer";
import Hotelmap from './component/hotelmap';
import Textinput from './component/textinput';
import Daypicker from './component/daypicker';
import Datepicker1 from './component/datepicker1';
import Searchbtn from './component/searchbtn';
import map from './../images/map.png';
import xiecheng from './../images/xiecheng.png';
import Cookies from "js-cookie";
export default class Hotel extends React.Component{
    constructor(props){
        super(props);
        this.state={
            hotelname:this.props.match.params.val,//
            datas:"",//
            searchval:"",//
            hoteldetail:"",//
            showmap:false,//
            devices:"",//
            hotelfilter:"",//
            hoteltotalnum:0,//
            hotelnum:0,//
            stararr:"",//
            comment:"",//
            positionarr:"",//
            maxprice:"",//
            minprice:"",//
            sort:1,//
            themearr:"",//
            devicesarr:"",//
            curcount:10,//
            pagetext:""
        };
    }
    componentWillMount(){
        // //
        // fetch("http://127.0.0.1:3001/api/hotel/hotel_list_filter_num?keyword="+this.state.hotelname,{method:"GET"})
        //     .then(res=>res.json()).then(res=>this.setState({hoteltotalnum:res.data}));
        // //
        // fetch("http://127.0.0.1:3001/api/hotel/hotel_list_filter_num?keyword="+this.state.hotelname,{method:"GET"})
        //     .then(res=>res.json()).then(res=>{
        //     if(res.success===0){
        //         return false;
        //     }else if(res.success===1){
        //         this.setState({hotelnum:res.data});
        //     }else{
        //         return false;
        //     }
        //
        // });
        //
        fetch("http://admin9.lohas-travel.com/api/hotel/hotel_list_filter?keyword="+this.state.hotelname+"&sort"+this.state.sort,{method:"GET"})
            .then(res=>res.json()).then(res=>{
                if(res.success===0){
                    alert(res.message);
                }else if(res.success===1){
                    this.setState({datas:res.data});
                }else{
                    return false;
                }
        });
        //
        fetch("http://127.0.0.1:3001/api/hotel/get_filter?keyword="+this.state.hotelname,{method:"GET"})
            .then(res=>res.json()).then(res=>this.setState({hotelfilter:res.data}));
    }
    componentDidUpdate() {
        const that = this;

        //id
        function getID(val){
            return document.getElementById(val);
        }

        //
        function timespicker(input,calendar,endinput){
            input.onfocus=function(){
                calendar.parentElement.style.display="block";
                calendar.style.display="block";
                let otd=calendar.getElementsByTagName('td');
                for(let i=0; i<otd.length; i++){
                    otd[i].onclick=function(){
                        //
                        let cur=Cookies.get('curday');
                        let time=this.getAttribute("data");
                        if(time<cur){
                            alert("");
                            time=cur;
                        }else{
                            time=this.getAttribute("data");
                        }
                        let year=time.substring(0,4);
                        let month=time.substring(4,6);
                        let day=time.substring(6,8);
                        input.value=year+"-"+month+"-"+day;
                        starttime=time;
                        if(endtime!==""&&starttime>endtime){
                            alert("");
                            endinput.value="";
                        }
                        calendar.style.display="none";
                        calendar.parentElement.style.display="none";
                    }
                }
            }
        }

        //
        function timespicker2(input,calendar,endinput){
            input.onfocus=function(){
                calendar.parentElement.style.display="block";
                calendar.style.display="block";
                let otd=calendar.getElementsByTagName('td');
                for(let i=0; i<otd.length; i++){
                    otd[i].onclick=function(){

                        //
                        let cur=Cookies.get('curday');
                        let time=this.getAttribute("data");
                        if(time<cur){
                            alert("");
                            time=cur;
                        }else{
                            time=this.getAttribute("data");
                        }
                        let year=time.substring(0,4);
                        let month=time.substring(4,6);
                        let day=time.substring(6,8);
                        input.value=year+"-"+month+"-"+day;
                        endtime=time;
                        if(endtime!==""&&starttime>endtime){
                            alert("");
                            endinput.value="";
                        }
                        calendar.style.display="none";
                        calendar.parentElement.style.display="none";
                    }
                }
            }
        }

        //
        let listitem = document.getElementsByClassName('listitem');
        for (let i = 0; i < listitem.length; i++) {
            listitem[i].onmouseenter = function () {
                let _this = this;
                let hotelpic = this.getElementsByClassName('hotelpic')[0];
                const hoteldetail = this.getElementsByClassName('hoteldetail')[0];
                hotelpic.onclick = function () {
                    hoteldetail.style.display = 'block';
                    let tablist = _this.getElementsByClassName('tablist')[0];
                    let listp = tablist.getElementsByTagName('p');
                    let detailbody = _this.getElementsByClassName('detailbody')[0];
                    let detailitem = detailbody.getElementsByClassName('detailitem');
                    for (let i = 0; i < listp.length; i++) {
                        listp[i].index = i;
                        listp[i].onclick = function () {
                            for (let i = 0; i < listp.length; i++) {
                                listp[i].className = "";
                            }
                            this.className = "active";
                            //
                            for (let i = 0; i < detailitem.length; i++) {
                                detailitem[i].style.display = "none";
                                detailitem[this.index].style.display = "block";
                            }
                        }
                    }
                }
            };
            listitem[i].onmouseleave = function () {
                that.setState({showmap: false});//markerdisplaynone0,
                let hoteldetail = this.getElementsByClassName('hoteldetail')[0];
                let tablist = this.getElementsByClassName('tablist')[0];
                let listp = tablist.getElementsByTagName('p');
                for (let i = 0; i < listp.length; i++) {
                    listp[i].className = "";
                }
                listp[0].className = "active";
                let detailbody = this.getElementsByClassName('detailbody')[0];
                let detailitem = detailbody.getElementsByClassName('detailitem');
                for (let i = 0; i < detailitem.length; i++) {
                    detailitem[i].style.display = "none";
                }
                detailitem[0].style.display = "block";
                hoteldetail.style.display = 'none';
            }
        }

        //
        let hotelsearch=getID('hotelsearch');
        let livetime=getID('livetime');
        let leavetime=getID('leavetime');
        let livecalendar=getID('livecalendar');
        let leavecalendar=getID('leavecalendar');
        let starttime="";
        let endtime="";

        // 
        //
        timespicker(livetime,livecalendar,leavetime);
        //
        timespicker2(leavetime,leavecalendar,leavetime);
        //
        let hotelbtn=getID('hotelbtn');
        hotelbtn.onclick=function(){
            if(hotelsearch.value===""){
                alert('');
            }else{
                that.setState({hotelname:hotelsearch.value,starttime:starttime,endtime:endtime});
                that.regethotelmun();
                that.regethotellist();
            }
        };
        //
        //js
        Array.prototype.indexOf=function(val){
            for(let i=0; i<this.length; i++){
                if(this[i]==val) return i;
            }
            return -1;
        };
        //js
        Array.prototype.remove = function(val) {
            let index = this.indexOf(val);
            if (index > -1) {
                this.splice(index, 1);
            }
        };
        //
        const hotelstar = getID('hotelstar');//
        const hoteltheme = getID('hoteltheme');//
        const hotelscore = getID('hotelscore');//
        const hotelprices = getID('hotelprices');//
        const hoteldevices = getID('hoteldevices');//
        const positionmarks = getID('positionmarks');//
        const hotelsort = getID('hotelsort');//
        const resetfilter = getID('resetfilter');//

        //
        hotelstar.onmouseenter= function () {
            const oSpan=this.getElementsByTagName('span');
            let arr=[];
            for(let i=0; i<oSpan.length; i++){
                oSpan[i].onclick=function(){
                    if(this.className==="active"){
                        this.className="";
                        arr.remove(this.getAttribute("data-id"));
                    }else{
                        this.className="active";
                        arr.push(this.getAttribute("data-id"));
                    }
                    that.setState({stararr:arr.toString()});
                    that.regethotelmun();
                    that.regethotellist();
                }
            }
        };
        //
        hoteltheme.onmouseenter= function () {
            const oSpan=this.getElementsByTagName('span');
            let arr=[];
            for(let i=0; i<oSpan.length; i++){
                oSpan[i].onclick=function(){
                    if(this.className==="active"){
                        this.className="";
                        arr.remove(this.getAttribute("data-id"));
                    }else{
                        this.className="active";
                        arr.push(this.getAttribute("data-id"));
                    }
                    that.setState({themearr:arr.toString()});
                    that.regethotelmun();
                    that.regethotellist();
                }
            }
        };
        //
        hotelscore.onmouseenter= function () {
            const oSpan=this.getElementsByTagName('span');
            let arr=[];
            for(let i=0; i<oSpan.length; i++){
                oSpan[i].onclick=function(){
                    if(this.className==="active"){
                        this.className="";
                        arr.remove(this.getAttribute("data-id"));
                    }else{
                        this.className="active";
                        arr.push(this.getAttribute("data-id"));
                    }
                    that.setState({comment:arr.toString()});
                    that.regethotelmun();
                    that.regethotellist();
                }
            }
        };
        //
        hotelprices.onmouseenter= function () {
            const oSpan=this.getElementsByTagName('span');
            for(let i=0; i<oSpan.length; i++){
                oSpan[i].onclick=function(){
                    for(let i=0; i<oSpan.length; i++){
                        oSpan[i].className="";
                    }
                    this.className="active";
                    that.setState({
                        maxprice:this.getAttribute("data-max"),
                        minprice:this.getAttribute("data-min")
                    });
                    that.regethotelmun();
                    that.regethotellist();
                }
            }
        };
        //
        hoteldevices.onmouseenter= function () {
            const oSpan=this.getElementsByTagName('span');
            let arr=[];
            for(let i=0; i<oSpan.length; i++){
                oSpan[i].onclick=function(){
                    if(this.className==="active"){
                        this.className="";
                        arr.remove(this.getAttribute("data-id"));
                    }else{
                        this.className="active";
                        arr.push(this.getAttribute("data-id"));
                    }
                    that.setState({devicesarr:arr.toString()});
                    that.regethotelmun();
                    that.regethotellist();
                }
            }
        };
        //
        positionmarks.onmouseenter=function(){
            const hotelmarks=this.getElementsByClassName("hotelmarks");
            const listwrapper=this.getElementsByClassName("listwrapper");
            let  arr=[];
            for(let i=0; i<hotelmarks.length; i++){
                hotelmarks[i].index=i;
                hotelmarks[i].onclick=function(){
                    if(listwrapper[this.index].style.display==="block"){
                        listwrapper[this.index].style.display="none";
                    }else{
                        listwrapper[this.index].style.display="block";
                    }
                    const oP=listwrapper[this.index].getElementsByTagName("p");
                    for(let i=0; i<oP.length; i++){
                        oP[i].onclick=function(){
                            if(this.className==="active"){
                                this.className="";
                                arr.remove(this.getAttribute('data-id'));
                            }else{
                                this.className="active";
                                arr.push(this.getAttribute('data-id'));
                            }
                            that.setState({positionarr:arr.toString()});
                            that.regethotelmun();
                            that.regethotellist();
                        }
                    }
                }
            }
        };
        //
        hotelsort.onmouseenter=function(){
            const option=this.getElementsByClassName("option")[0];
            const listwrapper=this.getElementsByClassName("listwrapper")[0];
            option.onclick=function(){
                if(listwrapper.style.display==="block"){
                    listwrapper.style.display="none";
                }else{
                    listwrapper.style.display="block";
                }
                const oP=listwrapper.getElementsByTagName("p");
                for(let i=0; i<oP.length; i++){
                    oP[i].onclick=function(){
                        const oS=option.getElementsByTagName('s')[0];
                        for(let i=0; i<oP.length; i++) {
                            oP[i].className = "";
                        }
                        oS.innerHTML=this.innerHTML;
                        this.className="active";
                        that.setState({sort:this.getAttribute('data-id')});
                        that.regethotelmun();
                        that.regethotellist();
                    }
                }
            }
        };
        //
        resetfilter.onclick=function(){
            function resetall(ELe,ELe2){
                const oELe2=ELe.getElementsByTagName(ELe2);
                for(let i=0; i<oELe2.length; i++) {
                    oELe2[i].className = "";
                }
            }
            resetall(hotelstar,"span");//
            resetall(hoteltheme,"span");//
            resetall(hotelscore,"span");//
            resetall(hotelprices,"span");//
            resetall(hoteldevices,"span");//
            resetall(positionmarks,"p");//
            resetall(hotelsort,"p");//
            const oS=hotelsort.getElementsByTagName('s')[0];
            oS.innerHTML="";
            that.setState({
                stararr:"",//
                comment:"",//
                positionarr:"",//
                maxprice:"",//
                minprice:"",//
                sort:1,//
                themearr:"",//
                devicesarr:""//
            });
            that.regethotelmun();
            that.regethotellist();
        };
    }
    //
    showmap(){
        this.setState({showmap:true});
    };
    getdetaildata(e){
        //
        fetch("http://admin9.lohas-travel.com/api/hotel/hotel_info_detail?id="+e.target.id,{method:"GET"})
            .then(res=>res.json())
            .then(res=>this.setState({hoteldetail:res.data}));
        //
        fetch("http://admin9.lohas-travel.com/api/hotel/get_hotel_devices?hotel_id="+e.target.id,{method:"GET"})
            .then(res=>res.json())
            .then(res=>this.setState({devices:res.data}));
    };
    //
    getdate(date){
        return moment(parseInt(date)*1000).format('YYYY-MM-DD');
    };
    //300
    detailtext(text){
        return text.substr(0,300);
    }
    //
    regethotellist(){
        fetch("http://127.0.0.1:3001/api/hotel/hotel_list_filter?keyword="
            +this.state.hotelname+"&star="+this.state.stararr+"&position="+this.state.positionarr
            +"&max="+this.state.maxprice+"&min="+this.state.minprice+"&sort="+this.state.sort
            +"&theme="+this.state.themearr+"&devices="+this.state.devicesarr+"&comment="
            +this.state.comment+"&count="+this.state.curcount,{method:"GET"})
            .then(res=>res.json())
            .then(res=>this.setState({datas:res.data}));
    }
    //
    regethotelmun(){
        fetch("http://127.0.0.1:3001/api/hotel/hotel_list_filter_num?keyword="
            +this.state.hotelname+"&star="+this.state.stararr+"&position="+this.state.positionarr
            +"&max="+this.state.maxprice+"&min="+this.state.minprice+"&sort="+this.state.sort
            +"&theme="+this.state.themearr+"&devices="+this.state.devicesarr+"&comment="
            +this.state.comment,{method:"GET"})
            .then(res=>res.json())
            .then(res=>this.setState({hotelnum:res.data}));
    }
    //
    openWindows(){

    }
    //
    pageadd(){
        if(this.state.curcount>50){
            this.setState({pagetext:""});
            return
        }
        this.setState({curcount:this.state.curcount+5});
        this.regethotellist();
    }
    render(){
        //
        const hotelname=this.state.hotelname;
        const hotellist=this.state.datas;//
        const detaildata=this.state.hoteldetail;//
        const devices=this.state.devices;//
        const hotelfilter=this.state.hotelfilter;//
        const position=hotelfilter.position;//
        const pagetext=this.state.pagetext;//
        const hotelnum=this.state.hotelnum;//
        const hoteltotalnum=this.state.hoteltotalnum;//
        let site=[];//
        let siteid=[];//id
        let station=[]; //
        let stationid=[];//id
        if(position){
            position.map(item=>{
                if(item.categoryid==="1"){
                    site.push(item.title);
                    siteid.push(item.id);
                }else if(item.categoryid==="2"){
                    station.push(item.title);
                    stationid.push(item.id);
                }else{
                    return false;
                }
            })
        }
        return(
            <div id="hotel">
                {/**/}
                <Header/>
                {/**/}
                {/**/}
                <div className="searchwrapper w">
                    <div className="input">
                        <Textinput placeholder="" wth="380px" id="hotelsearch"/>
                        <Datepicker1  id="livetime" placeholder="()"/>
                        <Datepicker1  id="leavetime" placeholder="()"/>
                    </div>
                    <Searchbtn id="hotelbtn"/>
                    {/**/}
                    <div className="datewrapper">
                        <Daypicker id="livecalendar"/>
                        <Daypicker id="leavecalendar"/>
                    </div>
                    {/**/}
                </div>
                {/**/}

                {/**/}
                <div className="wrapper">
                    <div className="w" style={{overflow:"hidden"}}>
                        {/**/}
                        <div className="filter">
                            {/**/}
                            <div className="checkmap">
                                <img src={map} alt=""/>
                                <Link to={"/map/"+hotelname}></Link>
                            </div>
                            {/**/
                            /**/}
                            <div id="hotelfilter">
                                <div className="title"></div>
                                <div className="fiterwrapper">
                                    <div className="item">
                                        <div className="itemtitle"></div>
                                        <div className="itemwrapper" id="hotelstar">
                                            <span data-id="7">5/</span>
                                            <span data-id="6">4/</span>
                                            <span data-id="5">3/</span>
                                            <span data-id="10"></span>
                                        </div>
                                    </div>
                                    <div className="item">
                                        <div className="itemtitle"></div>
                                        <div className="itemwrapper" id="hoteltheme">
                                            <span data-id="8"></span>
                                            <span data-id="11"></span>
                                            <span data-id="12"></span>
                                            <span data-id="13"></span>
                                            <span data-id="14"></span>
                                            <span data-id="15"></span>
                                            <span data-id="16"></span>
                                            <span data-id="17"></span>
                                        </div>
                                    </div>
                                    <div className="item">
                                        <div className="itemtitle"></div>
                                        <div className="itemwrapper" id="hotelscore">
                                            <span data-id="1">4.5-5.0</span>
                                            <span data-id="2">4.0-4.5</span>
                                            <span data-id="3">3.5-4.0</span>
                                            <span data-id="4">0-3.5</span>
                                        </div>
                                    </div>
                                    <div className="item">
                                        <div className="itemtitle"></div>
                                        <div className="itemwrapper" id="hotelprices">
                                            <span data-max="300" data-min="">300</span>
                                            <span data-max="800" data-min="300">300-800</span>
                                            <span data-max="1500" data-min="800">800-1500</span>
                                            <span data-max="" data-min="1500">1500</span>
                                        </div>
                                    </div>
                                    <div className="item">
                                        <div className="itemtitle"></div>
                                        <div className="itemwrapper" id="hoteldevices">
                                            {
                                                hotelfilter.devices?hotelfilter.devices.map((item,index)=><span key={index} data-id={item.id}>
                                                                                                            <img src={item.picture} alt=""/>
                                                                                                            <i>{item.title}</i>
                                                                                                        </span>
                                                ):""
                                            }
                                        </div>
                                    </div>
                                    {/**/}
                                    <div className="item">
                                        <div className="itemtitle"></div>
                                        <div className="itemwrapper" id="positionmarks">
                                            {/**/}
                                            <div className="hotelmarks" >
                                                <s></s>
                                                <i> </i>
                                            </div>
                                            <div className="listwrapper">
                                                {site.map((item,index)=><p key={index} data-id={siteid[index]}>{item}</p>)}
                                            </div>
                                            {/**/}
                                            {/**/}
                                            <div className="hotelmarks">
                                                <s></s>
                                                <i> </i>
                                            </div>
                                            <div className="listwrapper">
                                                {station.map((item,index)=><p key={index} data-id={stationid[index]}>{item}</p>)}
                                            </div>
                                            {/**/}
                                        </div>
                                    </div>
                                    {/**/}
                                </div>
                                <div id="resetfilter"></div>
                            </div>
                            {/**/}
                        </div>
                        {/**/}
                        {/**/}
                        <div className="hotellist">
                            {/**/}
                            <div className="headerfilter">
                                {/**/}
                                {/*<div className="info">{hotelnum}{hoteltotalnum}</div>*/}
                                {/**/}
                                <div className="sort" id="hotelsort">
                                    <div className="title"></div>
                                    <div className="option">
                                        <s></s>
                                        <i> </i>
                                    </div>
                                    <div className="listwrapper">
                                        <p data-id="1"></p>
                                        <p data-id="2"></p>
                                        <p data-id="3"></p>
                                        <p data-id="4"></p>
                                    </div>
                                </div>
                            </div>
                            {/**/}
                            {/**/}
                            <div className="lists">
                                {
                                    hotellist.length?hotellist.map((item,index)=>(
                                        <div className="listitem" key={index}>
                                            <div>
                                                <div className="hotelpic" style={{backgroundImage:"url("+item.image+")"}} id={item.id} ref="pics" onClick={this.getdetaildata.bind(this)}> </div>
                                                {/**/}
                                                <div className="information">
                                                    <h1 className="hotelname">{item.title}</h1>
                                                    <div className="category">
                                                        {item.category_list.map((item,index)=><span key={index}>{item.title}</span>)}
                                                    </div>
                                                    <div className="site">{item.position===null?"":""+item.position.title+Number(item.position.pos_distance).toFixed(1)+""}</div>
                                                    <div className="comment">
                                                        <span style={{backgroundColor:item.comment_color}}>{item.fake_comment}<i>{item.fake_comment_score}</i></span>
                                                        ({item.fake_comment_num})
                                                    </div>
                                                    <div className="shops">
                                                        {item.third_shop.map((item,index)=>
                                                            <p key={index}>
                                                                <a href={item.third_url} target="_blank" rel="noopener noreferrer">
                                                                    <span>{item.title}</span><span>{item.third_price}</span>
                                                                </a>
                                                            </p>
                                                        )}
                                                    </div>
                                                </div>
                                                {/**/}
                                                <div className="price">
                                                    <p className="shopname">{item.low_shop}</p>
                                                    <p className="maxprice">{item.sale.max}</p>
                                                    <p className="minprice">{item.sale.min}</p>
                                                    <a className="btn" href={item.low_url} target="_blank" rel="noopener noreferrer"></a>
                                                </div>
                                            </div>
                                            {/**/}
                                            <div className="hoteldetail">
                                                <div className="detailheader">
                                                    <div className="tablist">
                                                        <p className="active"></p>
                                                        <p className="maptab" onClick={this.showmap.bind(this)} ref={"map"+item.id}></p>
                                                        <p></p>
                                                    </div>
                                                    <div className="telephone">{detaildata.phone}</div>
                                                </div>
                                                <div className="detailbody">
                                                    {/**/}
                                                    <div className="detail detailitem" style={{display:"block"}}>
                                                        {/*<div ="detailpics">*/}{/**/}
                                                        {/*<p style={{backgroundImage:"url("+"http://admin8.lohas-travel.com/upload/5/98/55/59855ebc6395e682.jpg"+")"}}> </p>*/}
                                                        {/*<p style={{backgroundImage:"url("+"http://admin8.lohas-travel.com/upload/5/98/55/59855ebc6395e682.jpg"+")"}}> </p>*/}
                                                        {/*</div>*/}
                                                        <p className="text">
                                                            &nbsp;&nbsp;&nbsp;&nbsp;{this.detailtext(item.desc)}
                                                        </p>
                                                        <div className="devices">
                                                            <div className="devicestitle"></div>
                                                            <div className="devicesbody">
                                                                {
                                                                    devices?devices.map((item,index)=> <p key={index}>
                                                                                                            <img src={item.picture} alt=""/>
                                                                                                            <span>{item.title}</span>
                                                                                                        </p>):""
                                                                }
                                                            </div>
                                                        </div>
                                                    </div>
                                                    {/**/}
                                                    {/**/}
                                                    <div className="hotelmapwrapper detailitem">
                                                        {this.state.showmap?
                                                            <div>
                                                                <Hotelmap id={"mymap"+item.id} lat={item.lat} lng={item.lng} star={item.fake_comment_score} price={item.sale.min}/>
                                                                <div className="hoteladdress">{detaildata.address}</div>
                                                            </div>
                                                        :""}
                                                    </div>
                                                    {/**/}
                                                    {/**/}
                                                    <div className="hotelcomment detailitem">
                                                        {
                                                            detaildata.comment_lists?detaildata.comment_lists.map((item,index)=><div className="commentitem" key={index}>
                                                                                                            <div className="usericon" style={{backgroundImage:"url("+item.avatar+")"}}> </div>
                                                                                                            <div className="content">
                                                                                                                <div className="top">
                                                                                                                    <span className="score">{item.score_avg}</span>
                                                                                                                    <span className="time">{this.getdate(item.create_time)}</span>
                                                                                                                </div>
                                                                                                                <div className="commentcontent">
                                                                                                                    {item.content}
                                                                                                                 </div>
                                                                                                            </div>
                                                                                                        </div>):""
                                                        }
                                                    </div>
                                                    {/**/}
                                                </div>
                                            </div>
                                            {/**/}
                                        </div>
                                    )):""
                                }
                            </div>
                            {/**/}
                            {/**/}
                            <div id="pagemore" onClick={this.pageadd.bind(this)}><p>{pagetext}</p></div>
                            {/**/}
                        </div>
                        {/**/}
                        {/**/}
                        <div className="compare">
                            {/**/}
                            <div className="adv">
                                {/*<div className="btnwrapper">*/}
                                    {/*<i></i>*/}
                                    {/*<s id="closeadd"> </s>*/}
                                {/*</div>*/}
                            </div>
                            {/* */}
                            {/*<div className="pricecompare">*/}
                                {/*<div className="title"></div>*/}
                                {/*<div className="shopswrapper">*/}
                                    {/*<a href="http://www.ctrip.com/" target="_blank" rel="noopener noreferrer">*/}
                                        {/*<img src={xiecheng} alt=""/>*/}
                                    {/*</a>*/}
                                    {/*<a href="http://www.ctrip.com/" target="_blank" rel="noopener noreferrer">*/}
                                        {/*<img src={xiecheng} alt=""/>*/}
                                    {/*</a>*/}
                                    {/*<a href="http://www.ctrip.com/" target="_blank" rel="noopener noreferrer">*/}
                                        {/*<img src={xiecheng} alt=""/>*/}
                                    {/*</a>*/}
                                    {/*<a href="http://www.ctrip.com/" target="_blank" rel="noopener noreferrer">*/}
                                        {/*<img src={xiecheng} alt=""/>*/}
                                    {/*</a>*/}
                                    {/*<a href="http://www.ctrip.com/" target="_blank" rel="noopener noreferrer">*/}
                                        {/*<img src={xiecheng} alt=""/>*/}
                                    {/*</a>*/}
                                {/*</div>*/}
                                {/*<div className="btn" onClick={this.openWindows.bind(this)}></div>*/}
                            {/*</div>*/}
                        </div>
                        {/**/}
                    </div>
                </div>
                {/**/}
                {/**/}
                <Footer/>
                {/**/}
            </div>
        );
    }
}


