import React from 'react';
import payment from './../../images/payment.png';
import paycode from './../../images/paycode.png';
export default class Payment extends React.Component{
    render(){
        return(
            <div id="payment">
                <h2></h2>
                <img src={payment} alt=""/>
                <h2></h2>
                <p></p>
                <p>1105039809000041568</p>
                <p></p>
                <h2></h2>
                <p>lohaspay@lohas-travel.com</p>
                <img src={paycode} alt="" style={{width:150,marginLeft:20}}/>
                <p>1292184101</p>
            </div>
        );
    }
}


