import React from 'react';
import './../../css/daypicker.css';
import Cookies from 'js-cookie';
export default class Daypicker extends React.Component{
    componentDidMount(){
        const that=this;
        let calendar = document.getElementById(that.props.id);
        /*dateObj*/
        let dateObj=(function(){
            let _date=new Date();//
            return{
                getDate:function(){return _date;},
                setDate:function(date){_date=date;}
            }
        })();

        /*html*/
        function renderHtml() {
            let titleBox = document.createElement("div");// 
            let bodyBox = document.createElement("div");// 

            //html
            titleBox.className = "calendar-title-box";
            titleBox.innerHTML = "<span class='prev-month' > </span>" +
                "<span class='calendar-title'> </span>" +
                "<span class='next-month'> </span>";
            calendar.appendChild(titleBox);// calendar

            //html
            bodyBox.className = "calendar-body-box";
            const _headHtml = "<tr class='weeks'>" +
                "<th></th>" +
                "<th></th>" +
                "<th></th>" +
                "<th></th>" +
                "<th></th>" +
                "<th></th>" +
                "<th></th>" +
                "</tr>";//
            let _bodyHtml = "";

            //316,i6
            for (let i = 0; i < 6; i++) {
                _bodyHtml += "<tr>" +
                    "<td></td>" +
                    "<td></td>" +
                    "<td></td>" +
                    "<td></td>" +
                    "<td></td>" +
                    "<td></td>" +
                    "<td></td>" +
                    "</tr>";//
            }
            bodyBox.innerHTML = "<table class='calendar-table'>" + _headHtml + _bodyHtml + "</table>";
            calendar.appendChild(bodyBox);
        }

        /**/
        function showCalendarData(){
            let _year=dateObj.getDate().getFullYear();
            let _month=dateObj.getDate().getMonth()+1;
            let _dateStr=getDateStr(dateObj.getDate());//20180316

            //
            let calendarTitle=calendar.getElementsByClassName("calendar-title")[0];
            calendarTitle.innerText=_dateStr.substr(0,4)+""+_dateStr.substr(4,2)+"";

            //
            let _table=calendar.getElementsByClassName("calendar-table")[0];
            let _tds=_table.getElementsByTagName("td");
            let _firstDay=new Date(_year,_month-1,1);//
            for(let i=0; i<42; i++){
                let _thisDay=new Date(_year,_month-1,i+1-_firstDay.getDay());//41
                let _thisDayStr=getDateStr(_thisDay);//
                _tds[i].innerText=_thisDay.getDate();//42
                _tds[i].setAttribute('data',_thisDayStr);//dom
                if(_thisDayStr===getDateStr(new Date())){//
                    _tds[i].className="currentDay";
                    _tds[i].innerText="";
                    Cookies.set('curday',_thisDayStr);
                }else if(_thisDayStr.substr(0,6)===getDateStr(_firstDay).substr(0,6)){//
                    _tds[i].className="currentMonth";
                }else{//
                    _tds[i].className="otherMonth";
                }
            }
        }

        /*4+2+2*/
        function getDateStr(date){
            let _year=date.getFullYear();//dateObj
            let _month=date.getMonth()+1;//0
            let _d=date.getDate();
            _month=(_month>9)?(""+_month):("0"+_month);
            _d=(_d>9)?(""+_d):("0"+_d);
            return _year+_month+_d;
        }

        /**/
        function bindEvent(){
            let prevMonth=calendar.getElementsByClassName('prev-month')[0];
            let nextMonth=calendar.getElementsByClassName('next-month')[0];
            prevMonth.onclick=function(){
                let date=dateObj.getDate();
                dateObj.setDate(new Date(date.getFullYear(),date.getMonth()-1,1));
                showCalendarData();
            };
            nextMonth.onclick=function(){
                let date=dateObj.getDate();
                dateObj.setDate(new Date(date.getFullYear(),date.getMonth()+1,1));
                showCalendarData();
            };
        }

        renderHtml();   //calendar divhtml
        showCalendarData();//
        bindEvent();//
    }
    render(){
        return(<div className="datepickcalendar" data-time="" id={this.props.id}> </div>);
    }
}


