//
import React from 'react';
import './../../css/footer.css';
import {Link} from 'react-router-dom';
import free from './../../images/1_free.png';
import team from './../../images/2_team.png';
import africa from './../../images/3_africa.png';
import app from './../../images/4_app.png';
export default class Footer extends React.Component{
    render(){
        return(
            <div id="footer">
                <div className="w">
                    <div className="lists">
                        <div className="item webname"></div>
                        <div className="item">
                            <div className="listheader"></div>
                            <div className="listbody">
                                <Link to="/company"></Link>
                                <Link to="/company/cooprator"></Link>
                                <Link to="/company/recruit"></Link>
                                <Link to="/company/connect"></Link>
                            </div>
                        </div>
                        <div className="item">
                            <div className="listheader"></div>
                            <div className="listbody">
                                <Link to="/company/download">APP</Link>
                                <Link to="/company/law"></Link>
                                <Link to="/company/payment"></Link>
                            </div>
                        </div>
                        <div className="item">
                            <div className="listheader"></div>
                            <div className="listbody products">
                                <p>
                                    <a href="http://www.lohas-travel.com/" target="_blank" rel="noopener noreferrer"><img src={app} alt="APP"/></a>
                                    <a href="http://www.wildafrica.cn/" target="_blank" rel="noopener noreferrer"><img src={africa} alt=""/></a>
                                </p>
                                <p>
                                    <Link to="/"><img src={free} alt=""/></Link>
                                    <a href="http://wxskb.lvyouquan.cn/changzhoulehuo/Product/NewIndex" target="_blank" rel="noopener noreferrer"><img src={team} alt=""/></a>
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="copyright">CopyrightAll Right Reserved</div>
                </div>
            </div>
        );
    }
}


